import React from 'react';
import About from './about';
import ProjectsDetails from './projectDetails';
import VideoPlayer from './videoPlayer';
// import DownArrow from '../img/downArrow.png';

import {
  Accordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody,
} from 'react-accessible-accordion';

const AppAccordion = () => {

  // const tabStyle = {
  //   color: 'white',
  // }
  // const lineStyle = {
  //   borderStyle: 'dotted',
  //   borderImageSlice: '3% 3%',
  //   borderImageRepeat: 'round',
  // }
  // const ContainerStyle = {
  //   width: 'auto',
  //   animation: 'fadein .5s ease-in',
  // }
  // const accordionBody = {
  //   width: '100vw',
  //   animation: 'fadein .5s ease-in',
  // }

  return (
    
      <Accordion 
        accordion={false}>
        <AccordionItem>
          <AccordionItemTitle>
            <hr/>
            <h3 className="u-position-relative">
              ABOUT
            <div className="accordion__arrow" role="presentation" />
            </h3>
          </AccordionItemTitle>
          <AccordionItemBody>
            <About /> 
          </AccordionItemBody>
        </AccordionItem>
        <AccordionItem>
          <AccordionItemTitle>
            <VideoPlayer />
            <hr />

            MORE
          </AccordionItemTitle>
          <AccordionItemBody>
            <ProjectsDetails />
          </AccordionItemBody>
        </AccordionItem>
      </Accordion>
    
  )
};

export default AppAccordion;


